.logo{
    border-radius: 50%;
    width: 80px;
}

.top{
    box-shadow: rgba(0, 0, 0, 0.1) 0px 2px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
    width: 100%;
    height: 82px;
    background-color: white;
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    font-family: "Josefin Sans", sans-serif;
    z-index: 999;
}

.topLeft, .topRight{
    flex: 2;
    display: flex;
    align-items: center;
    justify-content: center;
}
.topCenter{
    flex: 8;
}

.topIcon{
    font-size: 20px;
    margin-right: 10px;
    color: #444;
    cursor: pointer;
}

.topImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    cursor: pointer;
}

.topList{
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none;
}
.topListItem{
    margin-right: 30px;
    font-size: 20px;
    font-weight: 300;
    cursor: pointer;
}

.topSearchIcon{
    font-size: 18px;
    color: #666;
    cursor: pointer;
    margin-left: 15px;
}
.topListItem:hover {
    color: #ff5533;
    font-weight: 500;
}
.sandwich{
      display: none;  
}
.top2{
    display: none;
}
.top2.showMenu {
    display: block;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 2px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
    width: 100%;
    height: 100%;
    background-color: white;
    position: sticky;
    top: 0;
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    margin-left: 20px;
    padding: 20px;
    font-family: "Josefin Sans", sans-serif;
    z-index: 999;
}
.fa-bars {
    cursor: pointer;
}

.logo2{
    border-radius: 50%;
    width: 120px;
}

.topImg2{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    /* object-fit: cover; */
    cursor: pointer;
}

.topList2{
    /* display: flex; */
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 30px;
    
}
.topListItem2{
    padding: 14px;
    /* margin-right: 30px; */
    font-size: 30px;
    font-weight: 300;
    cursor: pointer;
}

@media screen and (max-width: 700px) {
   .sandwich{
        display: block;
        margin: 30px;
        font-size: 40px;
   }
    .top{
        display: none;
    }
}